	   // indexDatas(){
		  //  // console.log('store',this.$store)
		  //  return this.$store.state.home.indexDatas
	   // },
	   // ...mapState(['indexDatas'])	没有模块化时候,可用
	   ...mapState({
		   indexDatas:state=>state.home.indexDatas
	   })
   },
   components:{
	   Recommend
   }
   
}
